}
.animated-link__text:nth-child(6) {
  transition-delay: 0.3s;
}
.animated-link__text:nth-child(7) {
  transition-delay: 0.35s;
}
.animated-link__text:nth-child(8) {
  transition-delay: 0.4s;
}
.animated-link__text:nth-child(9) {
