}
@keyframes translate-Y {
  0% {
    transform: translateY(2.5rem);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes dashoffset {
  from {
    opacity: 1;
    stroke-dashoffset: 308;
  }
  to {
    opacity: 1;
    stroke-dashoffset: 0;
  }
}
@keyframes dashoffset2 {
  from {
    opacity: 1;
    stroke-dasharray: 308;
    stroke-dashoffset: 0;
  }
  to {
    opacity: 1;
    stroke-dasharray: 308;
    stroke-dashoffset: 308;
  }
}
@keyframes dashoffsetforicons {
  0% {
    stroke-dashoffset: 400;
  }
  100% {
    stroke-dashoffset: 0;
  }
}
@keyframes dashoffsetforicons2 {
  0% {
    stroke-dashoffset: 0;
    stroke-dasharray: 400;
  }
  100% {
    stroke-dashoffset: 400;
    stroke-dasharray: 400;
  }
}
@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes maxHeight {
  from {
    max-height: 0;
  }
  to {
    max-height: 100vh;
  }
}
@keyframes mmHeaderPadding {
  from {
    padding: 0;
  }
  to {
    padding: 7rem 5rem 4.5rem;
  }
}
@font-face {
