}
html {
  width: 100%;
  height: auto;
  font-size: 16pt;
}
body {
  min-width: 320px;
  background-color: rgba(0, 0, 25, 0.9);
}
.wrapper {
  position: relative;
  width: 100%;
  z-index: 50;
}
.bg-full-window {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bg-full-window_works {
  /* background: @begin; */
  background-image: -webkit-gradient(linear, 0 0, 0 100%, from(rgba(0, 0, 0, 0.3)), color-stop(100%, rgba(0, 0, 0, 0.3))), url('../media/images/bg_works_list.jpg');
  background-image: -moz-linear-gradient(top, rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0.3) 100%), url('../media/images/bg_works_list.jpg');
  background-image: -o-linear-gradient(top, rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0.3) 100%), url('../media/images/bg_works_list.jpg');
  background-image: linear-gradient(top, rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0.3) 100%), url('../media/images/bg_works_list.jpg');
  background-size: cover, cover;
  background-position: 0 0 , center;
}
.bg-full-window__circle-div {
  width: 30rem;
  height: auto;
}
.bg-full-window__circle-div img {
  width: 100%;
  height: 100%;
}
.works-area {
  position: relative;
  padding-top: 10rem;
  z-index: 250;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}
.works-area__inner {
  width: 60rem;
}
.works-area__inner__link {
  width: 20rem;
  height: auto;
}
.works-area__inner__link_hidden {
