}
.works-area__inner__link img {
  width: 100%;
  height: auto;
  display: inline-block;
}
.works-area__inner__link_left {
  float: left;
  clear: both;
  margin-bottom: 2.5rem;
}
.works-area__inner__link_right {
  float: right;
  clear: both;
  margin-top: 2.5rem;
}
@media (max-width: 480px) {
  .bg-full-window__circle-div {
    width: 25rem;
  }
}
@media (max-width: 360px) {
  .bg-full-window__circle-div {
    width: 15rem;
  }
}
.header {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
