}
.nav-menu {
  list-style: none;
  position: absolute;
  z-index: 300;
  top: 50px;
  right: 50px;
}
.nav-menu__item {
  float: left;
  margin-left: 1.5rem;
}
.nav-menu__link {
  font-size: 0.75em;
  font-family: 'Josefin Sans', sans-serif;
  text-decoration: none;
  color: #fff;
}
.nav-menu__link:hover .nav-menu__link__atimated-text {
  -webkit-transform: rotateX(360deg);
  transform: rotateX(360deg);
}
.nav-menu__link__atimated-text {
  letter-spacing: -0.35em;
  font-size: 0.75em;
  text-transform: uppercase;
  display: inline-block;
  -webkit-transition: -webkit-transform 0.5s;
  transition: transform 0.4s;
}
.nav-menu__link__atimated-text:first-child {
  transition-delay: 0s;
}
.nav-menu__link__atimated-text:nth-child(1) {
  transition-delay: 0.05s;
}
.nav-menu__link__atimated-text:nth-child(2) {
  transition-delay: 0.1s;
}
.nav-menu__link__atimated-text:nth-child(3) {
  transition-delay: 0.15s;
}
.nav-menu__link__atimated-text:nth-child(4) {
  transition-delay: 0.2s;
}
.nav-menu__link__atimated-text:nth-child(5) {
  transition-delay: 0.25s;
}
.nav-menu__link__atimated-text:nth-child(6) {
  transition-delay: 0.3s;
}
.nav-menu__link__atimated-text:nth-child(7) {
  transition-delay: 0.35s;
}
.nav-menu__link__atimated-text:nth-child(8) {
  transition-delay: 0.4s;
}
.nav-menu__link__atimated-text:nth-child(9) {
  transition-delay: 0.45s;
}
.nav-menu__link__atimated-text:nth-child(10) {
  transition-delay: 0.5s;
}
.nav-menu__menu-button {
  font-family: 'Josefin Sans', sans-serif;
  color: #fff;
  width: 26px;
  height: 26px;
  display: none;
  cursor: pointer;
}
.nav-menu__menu-button_nohide {
  display: inline-block;
}
.nav-menu__menu-button_right {
  flex: right;
}
.nav-menu__menu-button__link {
  display: block;
}
.nav-menu__menu-button__line {
  position: absolute;
  left: 2px;
  width: 20px;
  height: 1px;
  background-color: #fff;
  display: inline-block;
}
.nav-menu__menu-button__line_dark {
  background-color: #fff;
}
.nav-menu__menu-button #line-1 {
  top: 4px;
}
.nav-menu__menu-button #line-2 {
  top: 12px;
}
.nav-menu__menu-button #line-3 {
