.row {
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100vh;
  z-index: 150;
  color: #000;
  background-color: #fff;
  background-repeat: repeat;
  background-size: cover;
}
.row__inner {
  padding-left: 12rem;
}
.row__inner_hidden {
  opacity: 0;
}
.row__inner_hidden .vertical-menu__item {
  transform: translateY(5rem);
}
.row__inner_hidden .gray-p {
  color: #bbb;
  font-family: 'Josefin Sans', sans-serif;
  text-transform: uppercase;
  font-size: 0.75em;
  transform: translateY(5rem);
}
.row__inner_hidden .underlined-div {
  padding-right: 0.35rem;
  display: inline-block;
  padding-bottom: 0.25rem;
  border-bottom: 1px solid #000;
  transform: translateY(5rem);
}
.row__inner_hidden.appeared {
  -webkit-transition: opacity 3s ease-in-out;
  -moz-transition: opacity 3s ease-in-out;
  -o-transition: opacity 3s ease-in-out;
  transition: opacity 3s ease-in-out;
  opacity: 1;
}
.row__inner_hidden.appeared .vertical-menu__item {
  transform: translateY(0);
  -webkit-transition: transform 1s ease-in-out;
  -moz-transition: transform 1s ease-in-out;
  -o-transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
}
.row__inner_hidden.appeared .vertical-menu__item:nth-child(1) {
  transition-delay: 0.3s;
}
.row__inner_hidden.appeared .vertical-menu__item:nth-child(2) {
  transition-delay: 0.6s;
}
.row__inner_hidden.appeared .vertical-menu__item:nth-child(3) {
  transition-delay: 0.9s;
}
.row__inner_hidden.appeared .vertical-menu__item:nth-child(4) {
  transition-delay: 1.2s;
}
.row__inner_hidden.appeared .vertical-menu__item:nth-child(5) {
  transition-delay: 1.5s;
}
.row__inner_hidden.appeared .gray-p {
  -webkit-transition: transform 1s ease-in-out;
  -moz-transition: transform 1s ease-in-out;
  -o-transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transform: translateY(0);
  transition-delay: 0;
}
.row__inner_hidden.appeared .underlined-div {
  -webkit-transition: transform 1s ease-in-out;
  -moz-transition: transform 1s ease-in-out;
  -o-transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transform: translateY(0);
  transition-delay: 1.5s;
}
.min-size-menu {
  position: relative;
  z-index: 200;
  display: flex;
  min-height: 100vh;
  align-items: stretch;
  justify-content: stretch;
}
.min-size-menu__header {
  max-height: 0;
  background-color: #fff;
  padding: 0;
}
.min-size-menu__header .vertical-menu__item {
  -webkit-transition: font-size 0.5s ease;
  -moz-transition: font-size 0.5s ease;
  -o-transition: font-size 0.5s ease;
  transition: font-size 0.5s ease;
}
.min-size-menu__footer {
  background-color: #1c1c1c;
  font-family: 'Josefin Sans', sans-serif;
  color: #fff;
  padding: 6rem;
  flex-grow: 1;
}
.min-size-menu__footer__block {
  margin-bottom: 2.5rem;
  opacity: 0;
  animation: opacity 2s ease-in;
  -webkit-transition: opacity 2s ease-in;
  -moz-transition: opacity 2s ease-in;
  -o-transition: opacity 2s ease-in;
  transition: opacity 2s ease-in;
}
.min-size-menu__footer__block__inner {
  margin: 2.5rem 0;
  padding: 0;
}
.min-size-menu__footer__text {
  letter-spacing: 0.25em;
  -webkit-transition: font-size 0.5s ease;
  -moz-transition: font-size 0.5s ease;
  -o-transition: font-size 0.5s ease;
  transition: font-size 0.5s ease;
}
.min-size-menu__footer__text_uppercased {
  text-transform: uppercase;
}
.min-size-menu.appeared .min-size-menu__header {
  animation: maxHeight 3s ease-out, mmHeaderPadding 1.5s ease-out;
  max-height: 100vh;
  padding: 7rem 5rem 4.5rem;
  -webkit-transition: max-height 3s ease-out, padding 0.5s linear;
  -moz-transition: max-height 3s ease-out, padding 0.5s linear;
  -o-transition: max-height 3s ease-out, padding 0.5s linear;
  transition: max-height 3s ease-out, padding 0.5s linear;
}
.min-size-menu.appeared .min-size-menu__footer__block {
  -webkit-transition: opacity 1s ease;
  -moz-transition: opacity 1s ease;
  -o-transition: opacity 1s ease;
  transition: opacity 1s ease;
  opacity: 1;
}
@media (max-width: 860px) {
  .min-size-menu {
    flex-direction: column;
  }
}
@media (max-width: 360px) {
  .min-size-menu__header {
    padding: 5rem 3.5rem 2rem;
  }
  .min-size-menu__footer {
    padding: 3.5rem;
  }
}
.flex-row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}
.flex-row__col {
  margin-right: 2rem;
}
.flex-row__heading {
  font-weight: 300;
  text-transform: capitalize;
  margin-bottom: 1.5rem;
  -webkit-transition: font-size 0.5s ease;
  -moz-transition: font-size 0.5s ease;
  -o-transition: font-size 0.5s ease;
  transition: font-size 0.5s ease;
}
.flex-row__link {
  color: #fff;
  font-size: 1em;
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: .15em;
  text-transform: uppercase;
  -webkit-transition: font-size 0.5s ease;
  -moz-transition: font-size 0.5s ease;
  -o-transition: font-size 0.5s ease;
  transition: font-size 0.5s ease;
}
.flex-row__img {
  -webkit-transition: width 0.5s ease, height 0.75s ease;
  -moz-transition: width 0.5s ease, height 0.75s ease;
  -o-transition: width 0.5s ease, height 0.75s ease;
  transition: width 0.5s ease, height 0.75s ease;
}
.flex-row__img_big {
  width: 30rem;
  height: 20rem;
}
.flex-row__img_medium {
  width: 15rem;
  height: 10rem;
}
.flex-row__img_small {
  width: 12rem;
  height: 8rem;
}
.icons {
  margin: 1.5rem 0;
  list-style: none;
}
.icons__item {
  margin: 0;
  padding: 0;
  display: inline-block;
}
.icons__link {
  display: flex;
  text-decoration: none;
  width: 50px;
  height: 50px;
  position: relative;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 50%;
}
.icons__svg {
  fill: #fff;
  width: 50%;
  height: 50%;
}
.icons__circle {
  position: absolute;
  width: 100%;
  height: 100%;
  stroke-dasharray: 0.05;
  stroke-dashoffset: 400;
  transform: rotate(270deg);
  animation: dashoffsetforicons2 0.5s linear;
  animation-fill-mode: forwards;
}
.icons__circle:hover {
  animation: dashoffsetforicons 0.5s linear;
  stroke-dasharray: 400;
  animation-fill-mode: forwards;
}
.block-hide {
  display: none;
}
#svg-2 {
  opacity: 0;
  animation: dashoffset2 1s linear;
}
.anim-group {
  position: absolute;
  top: calc(100vh - 13rem);
  left: calc(100vw - 18rem);
}
.anim-group_hidden {
  visibility: hidden;
}
.anim-group__text-container {
  position: absolute;
  left: -10.6px;
  text-decoration: none;
  font-size: 10px;
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400;
}
.anim-group__text-container__char {
  position: relative;
  color: #fff;
  display: inline-block;
  text-decoration: none;
  text-transform: uppercase;
  left: 0;
  transition: left 0.5s ease-out;
}
.anim-group__text-container#cont-1 {
  top: 30px;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(1) {
  transition-delay: 0.1s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(2) {
  transition-delay: 0.2s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(3) {
  transition-delay: 0.3s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(4) {
  transition-delay: 0.4s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(5) {
  transition-delay: 0.5s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(6) {
  transition-delay: 0.6s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(7) {
  transition-delay: 0.7s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(8) {
  transition-delay: 0.8s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(9) {
  transition-delay: 0.9s;
}
.anim-group__text-container#cont-1 .anim-group__text-container__char:nth-child(10) {
  transition-delay: 1s;
}
.anim-group__text-container#cont-2 {
  top: 46px;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(1) {
  transition-delay: 0.2s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(2) {
  transition-delay: 0.3s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(3) {
  transition-delay: 0.4s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(4) {
  transition-delay: 0.5s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(5) {
  transition-delay: 0.6s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(6) {
  transition-delay: 0.7s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(7) {
  transition-delay: 0.8s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(8) {
  transition-delay: 0.9s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(9) {
  transition-delay: 1s;
}
.anim-group__text-container#cont-2 .anim-group__text-container__char:nth-child(10) {
  transition-delay: 1.1s;
}
.anim-group__right-arrow {
  position: absolute;
  top: 65px;
  left: -10.6px;
  width: 64px;
  height: auto;
  transition: left 0.5s ease-out;
  transition-delay: 0.2s;
}
.anim-group:hover .anim-group__text-container__char {
  left: 25px;
}
.anim-group:hover .anim-group__right-arrow {
  left: 8px;
}
.svg-circle {
  position: relative;
  stroke-dasharray: 0.1;
  stroke-dashoffset: 308;
  transform: rotate(150deg);
}
.svg-circle:hover #svg-2 {
  stroke-dasharray: 308;
  animation: dashoffset 1s linear;
  animation-fill-mode: forwards;
  animation-direction: alternate;
}
@media (max-width: 1280px) {
  .anim-group {
    top: 68vh;
    left: calc(100vw - 18rem);
  }
}
@media (max-width: 980px) {
  .anim-group {
    top: 68vh;
    left: calc(100vw - 18rem);
  }
}
@media (max-width: 860px) {
  .anim-group {
    top: 68vh;
    left: calc(100vw - 12rem);
  }
}
@media (max-width: 480px) {
  .anim-group {
    visibility: hidden;
    top: 60vh;
    left: 55vw;
  }
  .anim-group__text-container#cont-1 {
    top: 0;
    left: -25vw;
  }
  .anim-group__text-container#cont-2 {
    top: 0;
    left: -5vw;
  }
  .anim-group__right-arrow {
    visibility: hidden;
  }
  .anim-group:hover .anim-group__text-container__char {
    left: 0;
  }
  .anim-group:hover .anim-group__right-arrow {
    left: 0;
  }
