@media (max-width: 360px) {
  .anim-group {
    visibility: hidden;
  }
}
.bottom-rotated-text {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 0.55em;
  position: absolute;
  top: calc(100vh - 100px);
  left: 55px;
  color: #fff;
  text-transform: uppercase;
  transform: rotate(-90deg);
  transform-origin: center left;
}
.bottom-arrow {
  position: absolute;
  top: calc(100vh - 70px);
  left: 50px;
  width: auto;
  height: 1rem;
}
.no-highlighted-link {
  color: #000;
  text-decoration: none;
}
.text-compact {
  letter-spacing: -0.35em;
}
